/**
 * define a 2d vector
 */
 export class Vector2 {
    x: number
    y: number
    constructor(x: number = 0, y: number = 0) {
        this.x = x
        this.y = y
    }
    set(x: number = 0, y: number = 0) {
        this.x = x
        this.y = y
    }
    copy(): Vector2 {
        return new Vector2(this.x, this.y)
    }
    asarray(): Array<number> {
        return [this.x, this.y]
    }
    asCSSPosition(): string {
        return `translateX(${this.x}px) translateY(${this.y}px)`
    }
    asCSSOrigin(): string {
        return `${this.x}px ${this.y}px`
    }
}

/**
 * define a 3d vector
 */
export class Vector3 {
    x: number
    y: number
    z: number
    constructor(x: number = 0, y: number = 0, z: number = 0) {
        this.x = x
        this.y = y
        this.z = z
    }
    set(x: number = 0, y: number = 0, z: number = 0) {
        this.x = x
        this.y = y
        this.z = z
    }
    copy(): Vector3 {
        return new Vector3(this.x, this.y, this.z)
    }
    asarray(): Array<number> {
        return [this.x, this.y, this.z]
    }
    sum(): number {
        return this.x+this.y+this.z
    }
    asCSSPosition(): string {
        return `translateX(${this.x}px) translateY(${this.y}px) translateZ(${this.z}px)`
    }
    asCSSRotation(): string {
        return `rotateX(${this.x}deg) rotateY(${this.y}deg) rotateZ(${this.z}deg)`
    }
}

/**
 * control geometrical behaviours of an individual card
 */
export class CardGeomControl {
    position: Vector3
    rotation: Vector3
    origin: Vector2
    height: number
    width: number
    borderRadius: number
    visibility: boolean
    classes: string
    constructor(position: Vector3, rotation: Vector3, origin: Vector2, height: number, width: number) {
        this.position = position
        this.rotation = rotation
        this.origin = origin
        this.height = height
        this.width = width
        this.borderRadius = 0
        this.visibility = true
        this.classes = ''
    }
}

export class CardGroupGeomControl {
    lids:  Array<CardGeomControl>
    cards: Array<CardGeomControl>
    constructor(cardSize: number) {
        this.lids = []
        this.cards = []
        for (let i = 0; i < 2; i++) {
            this.lids.push(
                new CardGeomControl(
                    new Vector3(),
                    new Vector3(),
                    new Vector2(),
                    cardSize,
                    cardSize
                )
            )
        }
        for (let i = 0; i < 4; i++) {
            this.cards.push(
                new CardGeomControl(
                    new Vector3(),
                    new Vector3(),
                    new Vector2(),
                    cardSize,
                    cardSize
                )
            )
        }
    }
}

export function changeCardGroupGeomControl_Cube_Net(cubeSize: number, cardGroupGeomControl: CardGroupGeomControl, angle: number, tilePadding: number) {
    let _sin = Math.sin(angle * Math.PI / 180)
    let _cos = Math.cos(angle * Math.PI / 180)
    let pad = tilePadding * (angle / 90)
    // 0
    cardGroupGeomControl.cards[0].position.set(-cubeSize-pad, 0, 0)
    cardGroupGeomControl.cards[0].rotation.set(-0, 90-angle, 0)
    cardGroupGeomControl.cards[0].origin.set(cubeSize, 0)
    // 1
    cardGroupGeomControl.cards[1].position.set(0, 0, 0)
    cardGroupGeomControl.cards[1].rotation.set(-0, 0, 0)
    cardGroupGeomControl.cards[1].origin.set(0, 0)
    // 2
    cardGroupGeomControl.cards[2].position.set(cubeSize+pad, 0, 0)
    cardGroupGeomControl.cards[2].rotation.set(-0, -90+angle, 0)
    cardGroupGeomControl.cards[2].origin.set(0, 0)
    // 3
    cardGroupGeomControl.cards[3].position.set(cubeSize+pad+_sin*(cubeSize+pad), 0, _cos*(cubeSize+pad))
    cardGroupGeomControl.cards[3].rotation.set(-0, -180+2*angle, 0)
    cardGroupGeomControl.cards[3].origin.set(0, 0)
    // upper lid
    cardGroupGeomControl.lids[0].position.set(0, -cubeSize-pad, 0),
    cardGroupGeomControl.lids[0].origin.set(0, cubeSize)
    cardGroupGeomControl.lids[0].rotation.set(-90+angle, 0, 0)
    // lower lid
    cardGroupGeomControl.lids[1].position.set(0, cubeSize+pad, 0),
    cardGroupGeomControl.lids[1].rotation.set(90-angle, 0, 0)
    cardGroupGeomControl.lids[1].origin.set(0, 0)
    // cardGroupGeomControl.cards.forEach(v => {
    //     v.borderRadius = angle
    // })
}
