<template>
    <div class="container-all">
        <div class="stats-panel">
            <p>
                {{$store.state.mouseX}}
            </p>
        </div>
        <div class="bg"></div>
        <div class="dynamic-container">
            <card-group
            ref="card-group"></card-group>
        </div>
    </div>
</template>

<script>
import CardGroup from '../components/CardGroup.vue'

export default {
    name: 'Home',
    components: {
        CardGroup,
    },
    data() {
        return {
        }
    },
    mounted() {
        window.addEventListener('mousemove', this.onMouseMove)
    },
    methods: {
        onMouseMove(event) {
            this.$store.commit('onMouseMove', event)
        }
    },
    beforeDestroy() {
        window.removeEventListener('mousemove', this.onMouseMove)
    }
}
</script>

<style scoped>
.full-container {
    background: #000;
}
.stats-panel {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 10;
}
.bg {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: #ccc;
    z-index: -1;
}
.dynamic-container {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    perspective: 1000px;
    transform-origin: 0 0 0;
    transform-style: preserve-3d;
    z-index: 2;
}
</style>
