import * as TWEEN from '@tweenjs/tween.js'
import store from '@/store'
import * as GEOM from '../GeomUtils'
var requestId = -1

export function loopAnim() {
    TWEEN.update()
    console.log('update')
    if (requestId >= 0) {
        requestId = window.requestAnimationFrame(loopAnim)
    }
}

export function startAnim() {
    if (requestId < 0) {
        requestId = window.requestAnimationFrame(loopAnim)
    }
}

export function stopAnim() {
    if (requestId >= 0) {
        window.cancelAnimationFrame(requestId)
        requestId = -1
    }
}

export function tweenNumber(
        fromValue: number,
        toValue: number,
        onUpdateFunc: (value: number) => any,
        onCompleteFunc: (value: number) => any,
        duration: number = 1000,
        easing: (amount: number) => number = TWEEN.Easing.Quadratic.InOut
    ): void {
        const v = { a: fromValue }
        store.dispatch('addTweenAnimObject')
        new TWEEN.Tween(v)
            .to({ a: toValue }, duration)
            .easing(easing)
            .onUpdate(() => onUpdateFunc(v.a))
            .onComplete(() => {
                store.dispatch('removeTweenAnimObject')
                onCompleteFunc(v.a)
            }).start()
    }


export function tweenVector2(
    fromValue: GEOM.Vector2,
    toValue: GEOM.Vector2,
    onUpdateFunc: (x: number, y: number) => any,
    onCompleteFunc: (x: number, y: number) => any,
    duration: number = 1000,
    easing: (amount: number) => number = TWEEN.Easing.Quadratic.InOut
): void {
    const v1 = { x: fromValue.x, y: fromValue.y }
    const v2 = { x: toValue.x, y: toValue.y }
    store.dispatch('addTweenAnimObject')
    new TWEEN.Tween(v1)
        .to(v2, duration)
        .easing(easing)
        .onUpdate(() => onUpdateFunc(v1.x, v1.y))
        .onComplete(() => {
            store.dispatch('removeTweenAnimObject')
            onCompleteFunc(v1.x, v1.y)
        }).start()
    }

export function tweenVector3(
    fromValue: GEOM.Vector3,
    toValue: GEOM.Vector3,
    onUpdateFunc: (x: number, y: number, z: number) => any,
    onCompleteFunc: (x: number, y: number, z: number) => any,
    duration: number = 1000,
    easing: (amount: number) => number = TWEEN.Easing.Quadratic.InOut
): void {
    const v1 = { x: fromValue.x, y: fromValue.y, z: fromValue.z }
    const v2 = { x: toValue.x, y: toValue.y, z: toValue.z }
    store.dispatch('addTweenAnimObject')
    new TWEEN.Tween(v1)
        .to(v2, duration)
        .easing(easing)
        .onUpdate(() => onUpdateFunc(v1.x, v1.y, v1.z))
        .onComplete(() => {
            store.dispatch('removeTweenAnimObject')
            onCompleteFunc(v1.x, v1.y, v1.z)
        }).start()
    }