





























































import Vue from 'vue'
import * as TWEEN from  '@tweenjs/tween.js'
import Card from './Card.vue'
import * as GEOM from '../scripts/GeomUtils'
import * as TweenUtils from '../scripts/AnimUtils/TweenUtils'

enum CardGroupPhase {
    Cube,
    CubeRotate,
    CubeNet1_half,
    CubeNet1_full,
    Carousel,
    List,
    Shuffle,
}

type Data = {
    cardGroupPosition: GEOM.Vector2,
    cardGroupGeomControl: GEOM.CardGroupGeomControl,
    defaultCubeSize: number,
    cubeNetAngle: number,
    isRotating: Boolean,
    onRotateEndFunc: Function,
    phase: CardGroupPhase,

    // card group centralize/decentralize
    isCenter: boolean,
    // store the original position
    originalCardGroupPosition: GEOM.Vector2,

}

export default Vue.extend({
    name: 'CardGroup',
    components: {
        Card,
    },
    data (): Data {
        return {
            cardGroupPosition: new GEOM.Vector2(0, 0),
            cardGroupGeomControl: new GEOM.CardGroupGeomControl(160),
            defaultCubeSize: 160,
            cubeNetAngle: 0,
            isRotating: false,
            onRotateEndFunc: () => {},
            phase: CardGroupPhase.Cube,
            isCenter: false,
            originalCardGroupPosition: new GEOM.Vector2(0, 0)
        }
    },
    created() {
        GEOM.changeCardGroupGeomControl_Cube_Net(this.defaultCubeSize, this.cardGroupGeomControl, 0, 10)
    },
    methods: {
        onAngleChange(): void {
            GEOM.changeCardGroupGeomControl_Cube_Net(this.defaultCubeSize, this.cardGroupGeomControl, this.cubeNetAngle-0, 20)
        },
        startRotate(): void {
            this.isRotating = true
            this.phase = CardGroupPhase.CubeRotate
        },
        cancelRotatingState() {
            this.isRotating = false
            this.phase = CardGroupPhase.Cube
            if(this.onRotateEndFunc instanceof Function) {
                this.onRotateEndFunc()
            }
            const cardsContainer = this.$refs['cards-container']
            if(cardsContainer instanceof Element) {
                cardsContainer.removeEventListener('animationiteration', this.cancelRotatingState);
            }
        },
        endRotate(onRotateEnd: Function): void {
            // https://stackoverflow.com/questions/35507472/stop-infinite-css3-animation-and-smoothly-revert-to-initial-state
            this.onRotateEndFunc = onRotateEnd
            const cardsContainer = this.$refs['cards-container']
            if(cardsContainer instanceof Element) {
                cardsContainer.addEventListener('animationiteration', this.cancelRotatingState);
            }
        },
        toCube(): void {
            const self = this
            TweenUtils.tweenNumber(
                this.cubeNetAngle-0,
                0,
                (v: number) => {
                    self.cubeNetAngle = v
                    self.onAngleChange()
                },
                () => {
                    self.phase = CardGroupPhase.Cube
                },
                1000,
                TWEEN.Easing.Exponential.InOut
            )
        },
        toCubeNet1_half(): void {
            const self = this
            TweenUtils.tweenNumber(
                this.cubeNetAngle-0,
                60,
                (v: number) => {
                    self.cubeNetAngle = v
                    self.onAngleChange()
                },
                () => {
                    self.phase = CardGroupPhase.CubeNet1_half
                },
                1000,
                TWEEN.Easing.Cubic.InOut
            )
        },
        toCubeNet1_full(): void {
            const self = this
            TweenUtils.tweenNumber(
                this.cubeNetAngle-0,
                90,
                (v: number) => {
                    self.cubeNetAngle = v
                    self.onAngleChange()
                },
                () => {
                    self.phase = CardGroupPhase.CubeNet1_full
                },
                1000,
                TWEEN.Easing.Bounce.Out
            )
        },
        moveToCenter(): void {
            // stores original position
            this.originalCardGroupPosition = this.cardGroupPosition.copy()
            // transform to center
            const self = this
            TweenUtils.tweenVector2(
                this.originalCardGroupPosition,
                new GEOM.Vector2(0, 0),
                (x: number, y: number) => {
                    self.cardGroupPosition.x = x
                    self.cardGroupPosition.y = y
                },
                () => {
                    self.isCenter = true
                },
                1000,
                TWEEN.Easing.Cubic.InOut
            )
        },
        moveToOriginalPosition(): void {
            if (this.isCenter === false) {
                return
            }
            const self = this
            TweenUtils.tweenVector2(
                this.cardGroupPosition,
                this.originalCardGroupPosition,
                (x: number, y: number) => {
                    self.cardGroupPosition.x = x
                    self.cardGroupPosition.y = y
                },
                () => {
                    self.isCenter = false
                },
                1000,
                TWEEN.Easing.Cubic.InOut
            )
        },
        toFullDetail() {
            this.cardGroupGeomControl.lids.forEach(l => {
                l.classes = 'smooth-transition'
            })
            this.cardGroupGeomControl.lids[0].classes += ' hide-top'
            this.cardGroupGeomControl.lids[1].classes += ' hide-bottom'
            this.cardGroupGeomControl.cards.forEach(l => {
                l.classes = 'smooth-transition'
            })
            this.cardGroupGeomControl.cards[0].classes += ' hide-left'
            this.cardGroupGeomControl.cards[1].classes += ' full-detail'
            this.cardGroupGeomControl.cards[2].classes += ' hide-right'
            this.cardGroupGeomControl.cards[3].classes += ' left-bar'
        },
        cancelFullDetail() {
            this.cardGroupGeomControl.lids.forEach(l => {
                l.classes = 'smooth-transition'
            })
            this.cardGroupGeomControl.cards.forEach(l => {
                l.classes = 'smooth-transition'
            })
            setTimeout(() => {
                this.cardGroupGeomControl.lids.forEach(l => {
                    l.classes = ''
                })
                this.cardGroupGeomControl.cards.forEach(l => {
                    l.classes = ''
                })
            }, 1010)
        },
        toCarousel() {
            this.cardGroupGeomControl.lids.forEach(v => {
                v.visibility = false
            })
            TweenUtils.tweenNumber(
                0,
                1,
                (v: number) => {
                    this.cardGroupGeomControl.cards.forEach(card => {
                        card.height = this.defaultCubeSize * (1 + v * 1)
                        card.width = this.defaultCubeSize * (1 + v * 0.4)
                    })
                    GEOM.changeCardGroupGeomControl_Cube_Net(
                        this.cardGroupGeomControl.cards[0].width,
                        this.cardGroupGeomControl,
                        v*110,
                        0
                    )
                },
                () => {
                    this.phase = CardGroupPhase.Carousel
                },
                1000,
                TWEEN.Easing.Cubic.InOut
            )
        },
        fromCarousel() {
            TweenUtils.tweenNumber(
                1,
                0,
                (v: number) => {
                    this.cardGroupGeomControl.cards.forEach(card => {
                        card.height = this.defaultCubeSize * (1 + v * 1)
                        card.width = this.defaultCubeSize * (1 + v * 0.4)
                    })
                    GEOM.changeCardGroupGeomControl_Cube_Net(
                        this.cardGroupGeomControl.cards[0].width,
                        this.cardGroupGeomControl,
                        v*110,
                        0
                    )
                },
                () => {
                    this.cardGroupGeomControl.lids.forEach(v => {
                        v.visibility = true
                    })
                    this.phase = CardGroupPhase.Cube
                },
                1000,
                TWEEN.Easing.Cubic.InOut
            )
        },
        toList() {
            this.cardGroupGeomControl.lids.forEach(l => {
                l.classes = 'smooth-transition card-as-list'
            })
            this.cardGroupGeomControl.cards.forEach(l => {
                l.classes = 'smooth-transition card-as-list'
            })
            this.cardGroupGeomControl.lids.forEach((v, i) => {
                // v.origin.set(this.defaultCubeSize/2, 0)
                v.rotation.set(Math.random()*30-15, -20 * ((i%2)-0.5) + Math.random()*30-15, Math.random()*30-15)
            })
            this.cardGroupGeomControl.cards.forEach((v, i) => {
                // v.origin.set(this.defaultCubeSize/2, 0)
                v.rotation.set(Math.random()*30-15, -20 * ((i%2)-0.5) + Math.random()*30-15, Math.random()*30-15)
            })
            const listItemDist = (this.defaultCubeSize + 20) / 2
            this.cardGroupGeomControl.lids[0].position.set(-listItemDist, -listItemDist*2, -10)
            this.cardGroupGeomControl.lids[1].position.set(listItemDist, -listItemDist*2, -10)
            this.cardGroupGeomControl.cards[0].position.set(-listItemDist, 0, -10)
            this.cardGroupGeomControl.cards[1].position.set(listItemDist, 0, -10)
            this.cardGroupGeomControl.cards[2].position.set(-listItemDist, listItemDist*2, -10)
            this.cardGroupGeomControl.cards[3].position.set(listItemDist, listItemDist*2, -10)
        },
        fromList() {
            GEOM.changeCardGroupGeomControl_Cube_Net(
                this.defaultCubeSize,
                this.cardGroupGeomControl, 0, 0)
            setTimeout(() => {
                this.cardGroupGeomControl.lids.forEach(l => {
                    l.classes = ''
                })
                this.cardGroupGeomControl.cards.forEach(l => {
                    l.classes = ''
                })
            }, 1010)
        }
    }
})

