import Vue from 'vue'
import Vuex from 'vuex'
import * as TweenUtils from '../scripts/AnimUtils/TweenUtils'

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        mouseX: 0,
        mouseY: 0,
        
        // TWEEN
        animationObjectCount: 0,
    },
    mutations: {
        onMouseMove (state, mouseMoveEvent) {
            state.mouseX = mouseMoveEvent.clientX
            state.mouseY = mouseMoveEvent.clientY
        },
        addAnimationObject(state) {
            state.animationObjectCount++
        },
        removeAnimationObject(state) {
            state.animationObjectCount--
        }
    },
    actions: {
        addTweenAnimObject(context) {
            if(context.state.animationObjectCount <= 0) {
                TweenUtils.startAnim()
            }
            context.commit('addAnimationObject')
        },
        removeTweenAnimObject(context) {
            if (context.state.animationObjectCount <= 0) {
                return
            }
            if(context.state.animationObjectCount == 1) {
                TweenUtils.stopAnim()
            }
            context.commit('removeAnimationObject')
        }
    }
})

export default store